import React from 'react';
import s from './ProfileInfo.module.css';
import Preloader from "../../common/Preloader/Preloader";
import userPhoto from "../../../img/9f08957fdb87a044dd6b8b5ddea8b024.jpg";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";

const ProfileInfo = ({profile, status, updateStatus, id}) => {
    if (!profile) {
        return <Preloader/>
    }
    return (

        <div>
            <div className={s.descriptionBlock}>
                <img alt="img" src={profile.photos.large != null ? profile.photos.large : userPhoto}/>
                <ProfileStatusWithHooks status={status} updateStatus={updateStatus}/>
                <div> Имя: {profile.fullName}</div>
                <div> ID: {id}</div>
            </div>

        </div>
    )
}

export default ProfileInfo;